.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  border-radius: 8px;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.gallery-section {
  padding: 60px 0;
}

.load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  height: 186px;
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.gallery-img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

